h1{
    
    text-align: center;
    font-size: 1.6rem !important;
    color: rgb(0, 204, 255);
    letter-spacing: 0.1px;
    background-color: black;
    height: 2em;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 5%;

}

.todoList{
    box-sizing: border-box;
    background-image:url(./assets/Images/back1.jpg);
    display:flex;
    flex-wrap:no-wrap;
    flex-direction:column;
    align-items: center;
    width:50%;
    justify-content: center;
    
   
    
    
    

}
.todoList ul{
    
    list-style-type:none;
    color:white;
    background: linear-gradient(90deg,rgb(255, 255, 255),rgb(255, 255, 255));
    width:100%;
    font-size:1rem;
    text-align: center;
    justify-content: center;
    border:5px solid white;
    overflow:hidden;
    height:100%;
    
}

.todoList li{
  
    padding-top: 2%;  
}


@media screen and (max-width:900px)
{
    .todoList{width:100%;margin-left:0;}
    

}
