* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html,
body {
  background-image:url('../src/assets/Images/back.jpg');

  font-family: "Quicksand", sans-serif;
  
}

::-webkit-scrollbar {
  width: 5px;
  background-color:rgb(255, 0, 0);
}

::-webkit-scrollbar-thumb {
  background-color:black; 
  border-radius: 10px;
}
