.todoForm label{
font-size: 1rem;
}
input[type="text"]{
    outline:none;
    border:0px;
    padding:1rem;
    width:100%;
    display: flex;
    align-content: center;
    font-size: 1.2rem; 
    background-color: rgb(255, 183, 189);
    text-transform: capitalize;
    margin-bottom:5%;
    margin-top:2%;
    border-radius: 5px;
}

input::placeholder{color:black;}

button{
    width:10%;
    height:2rem;
    border:0px;
    outline:none;
    background-color:black;
    -webkit-border-radius:5px;
    color:white;
    display:inline;
    margin:1% 0 1% 0;
    
}


#add{
    width:100%;
    margin:1% 0 5% 0;
    height:3rem;
    border:0px;
    outline:none;
    background-color:black;
    color:white;
}
button:hover{
    opacity:0.8;
    cursor:pointer;
}
.datePicker{
    background-color:white !important;
    border:2px solid rgb(255, 183, 189) !important;
    width:100% !important;
    height:1em;
    font-size: 0.8em !important;
   
    
    
}
.cat{
    width:100%;
    height: 3rem;
     background-color: rgb(255, 183, 189);
    border:2px solid rgb(255, 183, 189);
    font-size: 1.2rem; 
    background-color: rgb(255, 183, 189);
    border-radius:5px;
    

}

.fa-calendar{
    width:10%;
    height:100%;
}